
@import url(https://fonts.googleapis.com/css?family=Lato:400|Raleway:400);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body,
html {
  color: #2C2C2C;
  min-width: 600px;
  height: 100%;
  background: white;
  font: 400 1em 'Lato';
  -webkit-font-smoothing: antialiased;
}
#timeline {
  padding-top: 5%;
}
.timeline {
  height: 100%;
  position: relative;
}
.timeline canvas {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
}
.timeline figcaption {
    font: 900 190% 'Lato';
    text-transform: uppercase;
    -webkit-text-stroke: .25px;
}
.timeline h2 {
    font: 400 400% 'Raleway';
    padding-bottom: 100px;
    color: #b2cde9;
}
.timeline h6 {
    color: #0090F5;
    font: 400 80% Tahoma;
}
.timeline p,
  .timeline ol {
    font: 400 95% 'Raleway';
    padding: 3px 0 20px 0;
    color: #575757;
    text-align: justify;
    width: 70%;
}
.timeline ol {
    list-style: disc;
    margin-top: -20px;
    padding-left: 40px;
}
.timeline figure {
    float: right;
    width: 100%;
}
.timeline article {
    position: relative;
    width: 38%;
    overflow: hidden;
    margin-bottom: 100px;
}
.timeline article:first-of-type {
      float: left;
      text-align: right;
}
.timeline article:first-of-type p,
      .timeline article:first-of-type figure {
        float: right;
}
.timeline article:last-of-type {
      float: right;
}
.timeline article:last-of-type h2 {
        color: #c6e0aa;
}
.timeline article:last-of-type h6,
      .timeline article:last-of-type a {
        color: #40aa00;
}
.timeline article:last-of-type a:hover {
        color: #95D40D;
}



